.noDisplay{
    color: aliceblue;
    display: none;
}

@media only screen and (min-device-width:0px) and (max-device-width:1214.75px){
    .display{
        display: none;
    }
    .noDisplay{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100px;
        font-size: 1.5em;
        display: flex;
        color: red;
    }
}